import api from '../server'
import { mapState } from 'vuex'
export default{
    data(){
        return {
            IMGURL:api.BASEIMGURL,
            defaultImg: 'this.src="' + require('../assets/icon_yizhu@2x.png') + '"',//默认图片
            groups:[],
            doctors:[],
            noList:false,
            moregroup:false,
        }
    },
    created:function () {

    },
    computed:{
        openclose(){
            if(this.moregroup){
                return '收起'
            }else {
                return '加载更多'
            }
        }

    },
    watch:{

    },
    mounted(){
       this.getattion();
    },
    methods:{
        getattion(){
            const _this=this;
            api.getmydoclist({}).then(res=>{
                if(res.code=='000'){
                    var data = JSON.parse(res.data);
                    console.log(JSON.parse(res.data).doctors);
                    _this.groups=data.groups;
                    _this.doctors=data.doctors;
                    if(data.groups.length<1 && data.doctors.length<1){
                        _this.noList = true
                    }
                    $('body').show();
                }
            })
        },
        loadMore(){
           this.moregroup = !this.moregroup;
        }
    }
}



